import http from "../http-common";

class CommandsDataService {
  changeloglevel(id, data) {
    return http.post(`/commands/changeloglevel/${id}`, data);
  }

  //Quick LINKS commands
  startsrvrmgrserver(profile, data) {
    return http.post(`/commands/startsrvrmgrserver/${profile}`, data);
  }
  startsrvrmgrent(profile, data) {
    return http.post(`/commands/startsrvrmgrent/${profile}`, data);
  }
  //Component commands
  startupcomp(profile, data) {
    return http.post(`/commands/startupcomp/${profile}`, data);
  }
  shutdowncomp(profile, data) {
    return http.post(`/commands/shutdowncomp/${profile}`, data);
  }
  starttask(profile, data) {
    return http.post(`/commands/starttask/${profile}`, data);
  }
  killtask(profile, data) {
    return http.post(`/commands/killtask/${profile}`, data);
  }
  listtask(profile, data) {
    return http.post(`/commands/listtask/${profile}`, data);
  }

  //LOG Levels
  setlog(profile, data) {
    return http.post(`/commands/setlog/${profile}`, data);
  }

  invokeCommandByName(data) {
    return http.post(`/commands/executecommandbyname`, data);
  }

  //Parameter setting commands
  changeParameter(profile, data) {
    return http.post(`/commands/changeparameter/${profile}`, data);
  }

}

export default new CommandsDataService();