<template>
  <div>
  <v-data-table
    :headers="selectedHeaders"
    :items="showData"
    :loading="bLoading"
    :search="search"
    :items-per-page="10"
    loading-text="Loading... Please wait"
    class="elevation-24"
    v-model="selectedTemplate"
    item-key="CC_ALIAS"
    :single-expand="singleExpand"
    :expanded.sync="expanded"
    show-expand
  >
  <template v-slot:header.bigLogLevel="{ header }">
    <v-badge
      class = "my-6 px-2"
      :value="isDisabled"
      :content="isDisabledText"
      bordered
      color="warning"
    >  
    {{ header.text}}
    </v-badge>
  </template>

    <template v-slot:header.commands="{ header }">
      <v-badge
        class = "my-6 px-2"
        :value="isDisabled"
        :content="isDisabledText"
        bordered
        color="warning"
      >  
    {{ header.text}}
    </v-badge>
    </template>


    <template v-slot:top>
      <v-card>
        <v-card-title>
          Component events
          <v-spacer></v-spacer>
          <v-text-field
            v-model="search"
            label="search component"
            append-icon="mdi-text-search"
            single-line
            hide-details
            auto
          ></v-text-field>
          <v-spacer></v-spacer>
          <v-icon 
            large
            :color="getColor"
            @click="refresh()"
            >mdi-refresh 
          </v-icon>        
        </v-card-title>
      </v-card>
    </template>

    <template v-slot:expanded-item="{ headers }">
      <td :colspan="headers.length">
        <div class="d-flex justify-center">
        <v-card>
          <v-card-title>
            Events
            <v-spacer></v-spacer>
            <v-text-field
              v-model="searchEvent"
              append-icon="mdi-text-search"
              label="search event"
              single-line
              hide-details
              auto
            ></v-text-field>
          </v-card-title>
          <v-data-table
            hide-default-header
            dense
            :headers="headersEvents"
            :items="showDataEvents"
            :search="searchEvent"
            :items-per-page="5"
            sort-by="ET_ALIAS"
          >
            <template v-slot:[`item.logLevel`]="{ item }">
              <div class="d-flex align-center justify-center py-0 my-0">
                <v-text-field
                  :disabled="isDisabled"
                  v-if="isSelectedProfile"
                  single-line
                  v-model="item.logLevel"
                  :rules="[rules.minmax]"
                  class="shrink kozepes py-0 my-0"
                >
                  <v-icon
                    :disabled="isDisabled"
                    slot="prepend-inner"
                    @click="return item.logLevel--;"
                    small
                    color="green"
                    >mdi-minus
                  </v-icon>
                  <v-icon
                    :disabled="isDisabled"
                    small
                    slot="append"
                    @click="return item.logLevel++;"
                    color="red"
                    >mdi-plus
                  </v-icon>
                </v-text-field>
                <v-tooltip top>
                  <template v-slot:activator="{ on, attrs }">
                    <v-icon
                      :disabled="isDisabled"
                      big
                      class="ml-2"
                      :color="getColor"
                      @click="setLevel(item)"
                      :diasabled="isDisabled"
                      v-on="on"
                      v-bind="attrs"
                    >
                      mdi-send
                    </v-icon>
                  </template>
                  <span>Set log level</span>
                </v-tooltip>
              </div>
            </template>
          </v-data-table>
        </v-card>
        </div>
      </td>
    </template>

    <template v-slot:[`item.commands`]="{ item }">
      <template>
        <v-tooltip top>
          <template v-slot:activator="{ on, attrs }">
            <v-icon
              large
              class="mx-1"
              color="green"
              :disabled="isDisabled"
              @click="commandInvoke(item, 'START')"
              v-on="on"
              v-bind="attrs"
            >
              mdi-play
            </v-icon>
          </template>
          <span>Start component</span>
        </v-tooltip>

        <v-tooltip top>
          <template v-slot:activator="{ on, attrs }">
            <v-icon
              large
              class="mx-1"
              color="red"
              :disabled="isDisabled"
              @click="commandInvoke(item, 'STOP')"
              v-on="on"
              v-bind="attrs"
            >
              mdi-stop
            </v-icon>
          </template>
          <span>Stop component</span>
        </v-tooltip>

        <v-tooltip top>
          <template v-slot:activator="{ on, attrs }">
            <v-icon
              medium
              class="mx-1"
              color="primary"
              :disabled="isDisabled"
              @click="commandInvoke(item, 'LIST')"
              v-on="on"
              v-bind="attrs"
            >
              mdi-clipboard-text-outline
            </v-icon>
          </template>
          <span>List component task</span>
        </v-tooltip>

        <v-tooltip top>
          <template v-slot:activator="{ on, attrs }">
            <v-icon
              medium
              class="mx-1"
              color="primary"
              :disabled="isDisabled"
              @click="commandInvoke(item, 'TASK')"
              v-on="on"
              v-bind="attrs"
            >
              mdi-calendar-check-outline
            </v-icon>
          </template>
          <span>Start component task</span>
        </v-tooltip>

        <v-tooltip top>
          <template v-slot:activator="{ on, attrs }">
            <v-icon
              medium
              class="mx-1"
              color="purple"
              :disabled="isDisabled"
              @click="commandInvoke(item, 'KILL')"
              v-on="on"
              v-bind="attrs"
            >
              mdi-skull-outline
            </v-icon>
          </template>
          <span>Kill component</span>
        </v-tooltip>
      </template>
    </template>

    <template v-slot:[`item.bigLogLevel`]="{ item }">
      <template v-if="isSelectedProfile">
        <v-row align="center" justify="center">
          <v-col cols="12">
            <v-slider
              :disabled="isDisabled"
              v-model="item.bigLogLevel"
              :color="getColor"
              track-color='grey'
              step="1"
              thumb-label
              ticks
              min="1"
              max="5"
              max-width="40 px"
            >
              <template v-slot:append>
                <v-tooltip top>
                  <template v-slot:activator="{ on, attrs }">
                    <v-icon
                      big
                      class="ml-2"
                      :color="getColor"
                      :disabled="isDisabled"
                      @click="setCompLevel(item)"
                      v-on="on"
                      v-bind="attrs"
                    >
                      mdi-send
                    </v-icon>
                  </template>
                  <span>Set log level</span>
                </v-tooltip>
              </template>
            </v-slider>
          </v-col>
        </v-row>
      </template>
    </template>

    <template v-slot:no-data>
      <v-btn color="primary" @click="getAllData"> Reset </v-btn>
    </template>
  </v-data-table>
</div>
</template>
<script>

import CommandsDataService from "../services/CommandsDataService";
import {
  serLiveCDParams,
  eventLiveLog
} from "../services/ZookeeperLiveDataService";

const serCDParamsDataService      = new serLiveCDParams();
const eventLiveLogDataService     = new eventLiveLog();

export default {
  data: () => ({
    inputObject: {},
    initObject: {},
    inputData: [],
    expandedVal: [],
    singleExpand: true,
    search: "",
    searchEvent: "",
    selectedHeaders: [
      { text: "", value: "data-table-expand", width: "10%" },
      { text: "Server", value: "server", width: "20%", sortable: true},
      { text: "Component", value: "CC_ALIAS", align: "start", width: "20%", sortable: true},
      {
        text: "Set log level for all events",
        value: "bigLogLevel",
        align: "center",
        width: "20%",
        sortable: true,
        groupable: false,
      },
      {
        text: "Invoke",
        value: "commands",
        align: "center",
        width: "30%",
        sortable: false,
        groupable: false,
      },
    ],
    headersEvents: [
      { text: "Event", width: "70%", value: "ET_ALIAS"},
      { text: "Set Log Level", value: "logLevel", width: "30%", groupable: false, },
    ],

    rules: {
      minmax: (value) =>
        (value < 6 && value > 0) || "Value must be between 1-5",
    },
  }),

  computed: {
    isSelectedProfile () {
      if (!this.$store.state.selectedTemplate.name){return false}
      else {return this.$store.state.selectedTemplate.name}
    },
    selectedTemplate: {
      get() {
        return this.$store.state.selectedTemplate.name
          ? [this.$store.state.selectedTemplate.name]
          : [];
      },
      set() {
        this.refresh();
      },
    },
    expanded: {
      get() {return this.expandedVal;},
      set(value) {this.expandedVal = value;},
    },
    bLoading() {
      return this.inputData.length > 0 ? false : true
    },
    showData() {
      return this.inputData;
    },
    isDisabled() {
      return (this.$store.state.selectedTemplate.readonly || this.$store.state.trial) ? true : false
    },
    isDisabledText() {
      var text = "";
      if (this.$store.state.selectedTemplate.readonly){text = "READONLY"}
      if (this.$store.state.trial) {text = "TRIAL"}
      return text 
    },
    isTrial() {
      return this.$store.state.trial
    },
    showDataEvents () {
      return this.expandedVal.length > 0 ? this.expandedVal[0]["Events"] : []
    },
    getColor () {
      return (this.$store.state.selectedTemplate.color) ? this.$store.state.selectedTemplate.color : 'primary'
    },
  },

  watch: {
    isSelectedProfile (){
        this.refresh();
      }
  },

  created() {
    //
  },

  mounted() {
    this.getAllData();
  },

  methods: {
    async getAllData() {
      try {
        if(this.isSelectedProfile){
          const responseCCAlias     = await serCDParamsDataService.getAll(this.$store.state.selectedTemplate.name);
          const responseLiveEvents  = await eventLiveLogDataService.getAll(this.$store.state.selectedTemplate.name);

          //console.log(responseLiveEvents.data);

          //Get All component aliasname
          for (const server in responseCCAlias.data) {
            Object.keys(responseCCAlias.data[server]).forEach((comp) => {
              this.inputObject[server+'.'+comp] = {
                "server": server,
                "CC_ALIAS": comp,
                "bigLogLevel": 1,
                "Events": [],
              }
            })
          }

          //Set Events into components
          for (const server in responseLiveEvents.data) {
            for (const [comp, events] of Object.entries(responseLiveEvents.data[server])) {
              var maxLogLevel = 1
              const result = []
              for (const [etalias, loglvl] of Object.entries(events)) {
                if (loglvl>maxLogLevel){
                  maxLogLevel = loglvl
                }
                result.push({"ET_ALIAS": etalias, "logLevel": loglvl})
              }
              this.inputObject[server+'.'+comp] = {...this.inputObject[server+'.'+comp], "Events": result}
              if (maxLogLevel > 1) {this.inputObject[server+'.'+comp] = {...this.inputObject[server+'.'+comp], "bigLogLevel": maxLogLevel}}
            }
          }

          //Create table input data
          this.inputData = Object.values(this.inputObject);
          //DESC sort by Loglevel
          this.inputData.sort((a, b) => (b.bigLogLevel - a.bigLogLevel));
        }
      } catch (e) {
        console.log(e);
      }
    },

    async setCompLevel(item) {
      try {
        const data = {
          componentAlias: item.CC_ALIAS,
          logLevel: item.bigLogLevel,
        };
        await CommandsDataService.changeloglevel(this.$store.state.selectedTemplate.name, data);
      } catch (e) {
        console.log(e);
      }
    },

    async setLevel(item) {
      try {
        if (item.logLevel < 1 || item.logLevel > 5) {
          //console.log("log level rules stop");
          return;
        } else {
          const data = {
            eventAlias: item.ET_ALIAS,
            logLevel: item.logLevel,
            componentAlias: this.expandedVal[0].CC_ALIAS,
          };
          const response = await CommandsDataService.changeloglevel(this.$store.state.selectedTemplate.name, data);
          return response
        }
      } catch (e) {
        console.log(e);
      }
    },

    async commandInvoke(item, command) {
      const data = {
        componentAlias: item.CC_ALIAS,
        server: item.server,
      };
      var response;
      switch (command) {
        case "START":
          response = await CommandsDataService.startupcomp(this.$store.state.selectedTemplate.name, data);
          break;
        case "STOP":
          response = await CommandsDataService.shutdowncomp(this.$store.state.selectedTemplate.name, data);
          break;
        case "TASK":
          response = await CommandsDataService.starttask(this.$store.state.selectedTemplate.name, data);
          break;
        case "KILL":
          response = await CommandsDataService.killtask(this.$store.state.selectedTemplate.name, data);
          break;
        case "LIST":
          data.noExit = true;
          response = await CommandsDataService.listtask(this.$store.state.selectedTemplate.name, data);
          break;
      }
      return response
    },

    refresh() {
      this.inputData = [];
      this.expandedVal = []
      this.inputObject = [];
      this.getAllData();
    },
  },
};
</script>
<style>
.kozepes .v-text-field__slot input {
  text-align: center;
}

</style>